#ImageDeGarde-Container{
    width: 99.55vw;
}
#ImageDeGarde-Image{
    width: 99.55vw;
    position: absolute;
    top:0;
    left:0;
}

#TitreDebutDePage-grid {
    display: grid;
    grid-template-areas: "a b";
    grid-template-columns: 200px auto;
    grid-gap: 30px;
    align-items: center;
    position: absolute;
    left: calc((100% - var(--largeurNavigateur))/2 + 90px);
    top: 280px;

}

#TitreDebutDePage-UAlogo{
    grid-area: a;
}

#TitreDebutDePage-texte{
    grid-area: b;
    color:white;
    font-weight: lighter;
}

#TitreDebutDePage-h1{
    font-family: "Poiret One", serif;
    font-weight: lighter;
    font-size: 40px;
    margin: 0;
}

#TitreDebutDePage-h2{
    font-family: "Roboto Thin", sans-serif;
    font-weight: lighter;
    font-size: 30px;
    margin: 0;
}